<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import MessageComponent from "@/components/MessageComponent";
import { Config } from "@/shared/config";
import { getDateHour } from "@/shared/utils";
import { getDate } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  components: { MessageComponent },
  data() {
    return {
      basicinfo: {
        companyName: "",
        personIncharge: "",
        phoneNo: "",
        email: "",
        officeAddress: "",
      },
      reductioninfo: {
        supplier: "",
        proposalItem: "",
        status: "",
        account: "",
        expenseItem: "",
        proposedAmount: "",
        proposalDate: "",
        proposalPersonIncharge: "",
      },
      quotationinfo: {
        requestDate: "",
        processingDate: "",
        requestProcessIncharge: "",
        requestStatus: "",
      },
      contractInfo: {
        applicationDate: "",
        processingDate: "",
        status: "",
        contractStartDate: "",
        personIncharge: "",
      },
      supplierNo: "",
      reductionProposalNo: "",
      fileName: "",
      filePath: "",
      senderBelongs: "",
      personName: "",
      messageTitle: "",
      morningMessage: "",
      IconPath: "",
      replyMessage: "",
      messageList: [],
      aggrementNo: "",
      appUrl: Config.apiBaseUrl,
    };
  },
  computed: {
    ...mapGetters({
      client_details: "getClientData",
      PayLoad: "getQuotationNo",
    }),
  },
  mounted() {
    this.reductionProposalNo = this.PayLoad.Quotation_no;
    this.getRequestAppDetails();
    this.getMessageInfo();
  },

  methods: {
    /**
     * @description function to fetch the clientData reductionData and quotationData Based on the quotation_no reduction_no
     * @name getRequestAppDetails
     */     
    async getRequestAppDetails() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADCLIENTREDUCTIONPROPOSALDETAIL,
        {
          client_no: this.client_details.client_no,
          request_quotation_no: this.reductionProposalNo,
          reduction_proposal_no: this.PayLoad.Proposal_no,
        }
      );
      if (data.success) {
        let responseData = data.data.clientBaseInfoObject;//Fetching the ClientData
        let reductionData = data.data.reductionProposalObject;//Fetching the ReductionProposalData
        let quotationData = data.data.requestQuotationObject;//Fetching the QuotationData
        let contractData = data.data.agreementObject;//Fetching the ContractData
        let officeData = data.data.companyAddressObject;//Fetching the OfficeAddressData
        //Clientata
        this.basicinfo.companyName = responseData.client_name;
        this.basicinfo.personIncharge = responseData.client_person_name;
        this.basicinfo.phoneNo = responseData.client_tel;
        this.basicinfo.email = responseData.client_mail;
        this.basicinfo.officeAddress = officeData.value_string_small;
        // reductionData
        if (reductionData != null) {
          let Status;
          if (reductionData.reduction_proposal_sts_kbn == 1) {
            Status = this.$t("REDUCTONSTATUSKBN1");
          } else {
            Status = this.$t("REDUCTONSTATUSKBN2");
          }
          this.supplierNo = reductionData.supplier_no;
          this.reductioninfo.supplier = reductionData.supplier_name;
          this.reductioninfo.proposalItem =
            reductionData.reduction_proposal_name;
          this.reductioninfo.status = Status;
          this.reductioninfo.account = reductionData.accounts_name_nm;
          this.reductioninfo.expenseItem = reductionData.expense_item_nm;
          this.reductioninfo.proposedAmount =
            reductionData.reduction_proposal_cost;
          this.reductioninfo.proposalDate = !reductionData.created_at
            ? "-"
            : getDateHour(new Date(reductionData.created_at));
          this.reductioninfo.proposalPersonIncharge = reductionData.person_name;
        }
        if (quotationData != null) {
          //Quotation Info
          //QuotationStatus  is defined based on the workStatusNo
          let quotationStatus;
          if (quotationData.work_sts_kbn == "00") {
            quotationStatus = this.$t("UNPROCESSED");
          } else if (quotationData.work_sts_kbn == "01") {
            quotationStatus = this.$t("PROCESSING");
          } else {
            quotationStatus = this.$t("AgencyProcessed");
          }
          this.quotationinfo.requestDate =
            quotationData.request_quotation_dt == null
              ? "-"
              : getDateHour(new Date(quotationData.request_quotation_dt));
          this.quotationinfo.processingDate = !quotationData.work_dt
            ? "-"
            : getDateHour(new Date(quotationData.work_dt));
          this.quotationinfo.requestProcessIncharge = quotationData.person_name;
          this.quotationinfo.requestStatus = quotationStatus;
        }
        if (contractData != null) {
          //contractInfo
          this.contractInfo.applicationDate =
            contractData.agreement_request_date == null
              ? "-"
              : getDate(new Date(contractData.agreement_request_date));
          this.contractInfo.processingDate =
            contractData.work_dt == null
              ? "-"
              : getDate(new Date(contractData.work_dt));
          this.contractInfo.status = contractData.agreement_status_name;
          this.contractInfo.contractStartDate =
            contractData.agreement_start_date == null
              ? "-"
              : getDate(new Date(contractData.agreement_start_date));
          this.contractInfo.personIncharge = contractData.person_name;
          this.aggrementNo = contractData.agreement_no;
        }
      }
      this.$store.dispatch("setQuotationNo", {
        Quotation_no: this.PayLoad.Quotation_no,
        Proposal_no: this.PayLoad.Proposal_no,
        Supplier : this.PayLoad.Supplier,
        supplierNo : this.supplierNo
      });
    },
    /**
     * @description function to fetch the message information
     * @name getMessageInfo
     */ 
    async getMessageInfo() {
      let messages = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADCLIENTCOMMUNICATION,
        {
          client_no: this.client_details.client_no,
          reduction_proposal_no: this.PayLoad.Proposal_no,
          order_by: "desc",
        }
      );
      if (data.success) {
        let responseData = data.data.communicationListObject;
        responseData.forEach((item) => {
          let MessageDate = new Date(item.message_dt);
          MessageDate = new Date(
            MessageDate - MessageDate.getTimezoneOffset() * 1000 * 60
          );
          let MessageMonth = MessageDate.getMonth() + 1;
          let Day = MessageDate.getDate();
          let Time =
            ("0" + MessageDate.getHours()).slice(-2) +
            ":" +
            ("0" + MessageDate.getMinutes()).slice(-2);

          let iconPath = "";
          if (
            item.message_sender_icon_path &&
            item.message_sender_icon_path != "undefined" &&
            item.message_sender_icon_path != "null"
          ) {
            iconPath = this.appUrl + item.message_sender_icon_path;
          }
          let messageData = {
            messageNo: item.message_no,
            attached_file_data: item.attachments,
            senderBelongs: item.message_sender_belongs,
            personName: item.message_sender_person_name,
            messageTitle: item.message_title,
            morningMessage: item.message_text,
            IconPath: iconPath,
            AttchMentFlag: item.attached_file_flg,
            message_sender_account_type: item.message_sender_account_type,
            SupportType: item.client_support_type,
            Month: MessageMonth,
            Day: Day,
            Time: Time,
          };
          messages.push(messageData);
        });
        this.messageList = messages;
      }
    },
    /**
     * @description function to call  the previous page
     * @name goToPreviousPage
     */     
    goToPreviousPage() {
      this.$store.dispatch("setClientData", {
        client_name: this.client_details.client_name,
        client_no: this.client_details.client_no,
        tab_index: 6,
      });
      this.$router.push({ name: "client_process" });
    },
        /**
     * @description function to call the contractInfoAddEdit page
     * @name onContractEdit
     */ 
    onContractEdit() {
     //based the aggrementNo assigning the flag either update or create the record in contractInfoAddEdit page
      if (this.aggrementNo == "") {
        this.$store.dispatch("setAggrementNo", {
          flagcheck: true,
          reduction_proposal_no: this.PayLoad.Proposal_no,
          page: "CRM-70",
        });
      } else {
        this.$store.dispatch("setAggrementNo", {
          aggrementno: this.aggrementNo,
          flagcheck: false,
          page: "CRM-70",
        });
      }
      this.$router.push({ name: "client_mgmt_contract_info_add_edit" });
    },
    /**
     * @description function to call the clientTransactionAdd page
     * @name onTransactionAdd
     */     
    onTransactionAdd() {
      this.$store.dispatch("setAssesmentNo", {
        assesmentno: "",
      });
      this.$router.push({ name: "client_mgmt_client_transaction_add" });
    },
  },
};
</script>

<style scoped>
.hrType {
  border-width: 10;
  border-top: 1px dotted gray;
  border-bottom: 1px dotted gray;
}
.imageSize {
  height: 30px;
  width: 30px;
}
.align {
  white-space: pre-wrap;
}
</style>
